@mixin heading {
  color: $color-primary;
  font-family: $ff-AlumniSans;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  line-height: normal;
  margin: 0;

  @include vp-1199 {}

  @include vp-767 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-319 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}
