}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../../fonts/alumni-sans-v16-cyrillic_latin-700.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.about__wrapper {
  position: relative;
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
  padding-top: 120px;
  padding-left: 163px;
  display: grid;
  grid-template-columns: 1fr 660px;
  column-gap: 80px;
}
@media (max-width: 1199px) {
  .about__wrapper {
    padding: 0 45px;
    grid-template-columns: 1fr;
    justify-items: center;
    padding-top: 65px;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    padding: 0;
    padding-top: 37px;
  }
}

.about__video {
  grid-column: span 2;
  margin: 0 auto;
  transform: translate(0, -50%);
}
@media (max-width: 1199px) {
  .about__video {
    grid-column: span 1;
    width: 266px;
    height: 167px;
  }
}
@media (max-width: 767px) {
  .about__video {
    transform: translate(0);
    width: 100%;
    height: 167px;
  }
}

.about__description {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .about__description {
    margin-bottom: 43px;
    text-align: center;
    padding: 0 44px;
  }
}
@media (max-width: 767px) {
  .about__description {
    padding: 0 15px;
    text-align: left;
    margin-bottom: 33px;
  }
}

.about__description h2 {
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  line-height: normal;
  margin: 0;
  margin-bottom: 7px;
  font-size: 48px;
  letter-spacing: 1px;
}
@media (max-width: 767px) {
  .about__description h2 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 319px) {
  .about__description h2 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}
@media (max-width: 1199px) {
  .about__description h2 {
    font-size: 42px;
    font-weight: 600;
    letter-spacing: 1px;
    margin-left: -16px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .about__description h2 {
    font-size: 32px;
    letter-spacing: 0.96px;
    margin-left: 0;
    margin-bottom: 7px;
  }
}

.about__description p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 1199px) {
  .about__description p {
    margin-bottom: 33px;
  }
}
@media (max-width: 767px) {
  .about__description p {
    margin-bottom: 22px;
  }
}

.about__gym-description {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 7px;
}
.about__gym-description p {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
}
.about__gym-description p:nth-child(2) {
  display: block;
}
@media (max-width: 1199px) {
  .about__gym-description p:nth-child(2) {
    display: none;
  }
}
@media (max-width: 767px) {
  .about__gym-description {
    padding-right: 22px;
  }
}

@media (max-width: 767px) {
  .about__picture {
    margin-bottom: 40px;
  }
}

.about__picture img {
  height: 345px;
  width: 680px;
  object-fit: contain;
}
@media (max-width: 767px) {
  .about__picture img {
    object-fit: cover;
    height: 313px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  justify-content: center;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.6px;
  font-weight: 700;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:hover, .btn:focus {
  border: none;
  color: #103aae;
}
.btn:active {
  border: none;
  color: #102654;
}
.btn--dark {
  outline: 2px solid #102654;
  outline-offset: -2px;
  border: none;
  color: #102654;
  padding: 27px;
  box-sizing: border-box;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  font-size: 12px;
  font-weight: 700;
  background-color: transparent;
}
.btn--dark:hover, .btn--dark:focus {
  outline: none;
  color: #ffffff;
  background: linear-gradient(90deg, #ed0233 0%, #003eb7 100%);
}
.btn--dark:active {
  border: none;
  color: rgba(255, 255, 255, 0.3);
}
.btn--dark:disabled {
  opacity: 0.3;
}

.card {
  list-style: none;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .card {
    padding: 8.8% 15.3%;
  }
}
.card--is-active {
  border-image: linear-gradient(90deg, #ed0233 0%, #003eb7 100%) 2/7px;
}
.card--is-active .btn--dark {
  outline: none;
  color: #ffffff;
  background: linear-gradient(90deg, #ed0233 0%, #003eb7 100%);
}
@media (hover: hover) {
  .card:hover:hover:not(.focus-visible) {
    border-image: linear-gradient(90deg, #ed0233 0%, #003eb7 100%) 2/7px;
  }
}
.card:hover.focus-visible:focus {
  border-image: linear-gradient(90deg, #ed0233 0%, #003eb7 100%) 2/7px;
}
.card:hover .btn--dark {
  outline: none;
  color: #ffffff;
  background: linear-gradient(90deg, #ed0233 0%, #003eb7 100%);
}

.card h3 {
  margin: 0;
  text-align: center;
  font-size: 26px;
  font-weight: 700;
  line-height: 110.156%;
  margin-bottom: 22px;
  text-align: center;
}
@media (max-width: 767px) {
  .card h3 {
    font-size: 22px;
  }
}

.card p {
  margin: 0;
  font-size: 16px;
  line-height: 150%;
  margin-bottom: 96px;
  text-align: center;
}

.card div {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 59px;
  align-self: center;
  width: 100%;
}

.card div span {
  color: #e8e8e8;
  font-size: 100px;
  font-weight: 700;
  line-height: 1.5;
  opacity: 0.48;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -40%);
}

.card div span:first-child {
  color: #103aae;
  position: relative;
  text-align: center;
  font-size: 60px;
  font-weight: 700;
  line-height: 150%;
  opacity: 1;
  z-index: 2;
  width: 100%;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
